/**
 * 注意: 不能将参数state重新赋值，只能更改变量，否则会导致state更新失败
 * eg.禁止 state = { ...state, ...params }
 */
export default {
  getCurrentChannel (state, currentChannel) {
    state.currentChannel = currentChannel
  },
  getSearchContent (state, searchContent) {
    state.searchContent = searchContent
  },
  getSearchBarBlock (state, searchBarBlock) {
    state.searchBarBlock = searchBarBlock
  },
  getBack (state, returnSearchPageBlock) {
    state.returnSearchPageBlock = returnSearchPageBlock
  },
  getLabelsType (state, labelsType) {
    state.labelsType = labelsType
  },
  getLabelsIndex (state, labelsIndex) {
    state.labelsIndex = labelsIndex
  },
  // obtainBEDetailsName (state, bEDetailsName) {
  //   state.bEDetailsName = bEDetailsName
  // }

  setModuleAndModuleSearchType (state, payload) {

    state.module = payload.module
    state.moduleSearchType = payload.moduleSearchType

    gstorage.save('search')
  },

  setSearchType (state, searchType) {
    state.searchType = searchType

    gstorage.save('search')
  },

  // 如果需要刷新重载数据，必须重载此函数
  reloadFromStorage (state, reloadData) {
    // state = { ...state, ...reloadData}
    // state.module = reloadData.module
    // state.moduleSearchType = reloadData.moduleSearchType
    // state.searchType = reloadData.searchType

    gutils.merge(state, reloadData)
  }
}



