/****************************************分类规则**************************************************/
/**
 * 默认的分类规则
 * @type {{title: string, isDefault: boolean}}
 */
const defaultClassifyRule = [
    {
        title: '基础数据',
        equals: [],             // 必然包含的字段, isDefault = true时，所有剩余的字段都放到当前字段下
        contains: [],           // 必然包含的字段(包含字段名字. contains:['name'] => stu_name √ )
        isDefault: true,
    }
]

// 有错误的分类。 eg.查询超时、查询异常等结果
const defaultErrorClassifyRule = [
    {
        ...defaultClassifyRule,
        title: '错误提示'
    }
]


/****************************************第三方分类**************************************************/

