/**
 * 获得数据分类的规则
 * @param search_type
 * @param defaultRule
 * @returns {*}
 */
function getClassifyRule(search_type, defaultRule = defaultClassifyRule) {
    let rule
    switch(search_type) {
        case gconst.SearchType.PHONE_STATE: {
            rule = phoneStateClassifyRule
        }
            break
        case gconst.SearchType.BANK_PLUS: {
            rule = bankClassifyRule
        }
        break
        case gconst.SearchType.WEIBO: {
            rule = weiboFacebookTwitterClassifyRule
        }
        break
        case gconst.SearchType.IP_PLUS: {
            rule = ipPlusClassifyRule
        }
            break
        case gconst.SearchType.FACEBOOK: {
            rule = faceBookClassifyRule
        }
            break
        case gconst.SearchType.LOGISTICS_REALTIME: {
            rule = logisticsRealtimeClassifyRule
        }
            break
        case gconst.SearchType.NETWORK_TRACES: {
            rule = netTracesClassifyRule
        }
            break
        case gconst.SearchType.TWITTER: {
            rule = twitterClassifyRule
        }
            break
        case gconst.SearchType.WHOIS: {
            rule = whoisClassifyRule
        }
            break
        case gconst.SearchType.VIN: {
            rule = vinClassifyRule
        }
            break
        case gconst.SearchType.LOGISTICS: {
            rule = logisticsClassifyRule
        }
            break
        case gconst.SearchType.IP: {
            rule = ipClassifyRule
        }
            break
        case gconst.SearchType.BANK_REAL: {
            rule = bankRealClassifyRule
        }
            break
        case gconst.SearchType.IDCARD_PARSE: {
            rule = idcardParesClassifyRule
        }
            break
        default: {
            rule = defaultRule
        }
    }

    if (!rule)
        rule = defaultClassifyRule

    return rule
}

export {
    defaultClassifyRule,
    defaultErrorClassifyRule,
    phoneStateClassifyRule
}

export default getClassifyRule



