/**
 * 注意: 不能将参数state重新赋值，只能更改变量，否则会导致state更新失败
 * eg.禁止 state = { ...state, ...params }
 */
export default {
  initMe (state, me) {
    // // 仅做测试
    // me = {
    //   ...me,
    //   account: "pd0000001",
    //   realName: "雪山飞狐",
    //   mobilePhone: "15812345678",
    // }

    for (let k in me) {
      state[k] = me[k]
    }

    gstorage.save('me')
  },

  // 如果需要刷新重载数据，必须重载此函数
  reloadFromStorage (state, reloadData) {
    // reloadData.realName = '疯狂的快活吧'
    // vue.$store.dispatch('me/initMe', reloadData)

    console.log("reloadData:" + JSON.stringify(reloadData))

    gutils.merge(state, reloadData)
  }
}



