/**
 * 全局公用函数
 *
 * 调用:
    算法相关集合
 */


let UtilsAlgorithm = {

}


/**
 * 数据分组，算法一类
 * @param array [0, 1, 2, 3, 4, 5, 6]
 * @param itemsPerRow 3
 * @returns {Array} [[0, 1, 2], [3, 4, 5], [6]]
 */
UtilsAlgorithm.groupItems = UtilsAlgorithm.chunkArray = function chunkArray(array, size) {
    return array.reduce((acc, val) => {
        if (acc.length === 0) acc.push([])

        const last = acc[acc.length - 1]
        if (last.length < size) {
            last.push(val)
        } else {
            acc.push([val])
        }
        return acc
    }, [])
}


export default UtilsAlgorithm











































