import {api, base} from './api/api'
import Constant from './Constant'
import GlobalFuncs from './GlobalFuncs'
import { Utils, CalendarUtils, CityCodeUtils, IDCardNoUtils } from './utils'
import LocalStorageManager from './storage/LocalStorageManager'

window.gapi = api
window.gbase = base
window.gconst = Constant
window.gutils = Utils
window.gfuncs = GlobalFuncs
window.gstorage = LocalStorageManager

export default {
  gbase,
  api,
  constant: Constant,
  utils: Utils,
  funcs: GlobalFuncs,
  storage: LocalStorageManager,
}



