/**
 * 1.模块调用: this.$store.getters['me/getName']
 * 2.mapGetters映射: ...mapGetters('me', ['getName']),
 * 参考: https://blog.csdn.net/qq_39523111/article/details/79638614
 */
export default {
  demoGetName: state => state.demoName,
  demoGetAge: state => state.demoAge,
  other: state => `My name is ${state.demoName}, i am ${state.demoAge}.`
}



