export default {
  getActiveIndex: state => state.activeIndex,

  getTags: state => state.tags,

  getTag: (state, getters) => index => {
    return state.tags[index]
  },

  isExistTag: (state, searchType, searchKey) => {
    let tag = gfuncs.geneSearchTag(searchType, searchKey)
    return state.tags.contains(tag)
  }
}



