/**
 * 注意: 不能将参数state重新赋值，只能更改变量，否则会导致state更新失败
 * eg.禁止 state = { ...state, ...params }
 */
export default {
  setActiveIndex (store, index) {
    store.activeIndex = index

    gstorage.save('tags')
  },

  /**
   * 添加tag 如果已存在，把tag移到最后方
   * @param state
   * @param searchType
   * @param searchKey
   */
  addTag (state, tag) {
    const { searchKey, searchType } = tag
    let tags = state.tags

    let stag = gfuncs.geneSearchTag(searchType, searchKey)

    let idx = tags.indexOf(stag)
    if (idx >= 0)
      tags.splice(idx, 1)

    tags.push(stag)
    state.activeIndex = tags.length - 1

    gstorage.save('tags')
  },

  closeTag (state, index) {
    let tags = state.tags
    tags.splice(index, 1)

    if (state.activeIndex >= tags.length)
      state.activeIndex = tags.length - 1

    gstorage.save('tags')
  },

  closeOtherTags (state) {
    let tag = state.tags[state.activeIndex]
    state.tags = [tag]

    if (state.activeIndex >= 0 && state.tags.length > 0) {
      state.activeIndex = 0
    } else {
      state.activeIndex = -1
    }

    gstorage.save('tags')
  },

  closeAllTags (state) {
    state.tags = []
    state.activeIndex = -1

    gstorage.save('tags')
  },

  reloadFromStorage (state, reloadData) {
    // state = { ...state, ...reloadData}
    // state.module = reloadData.module
    // state.moduleSearchType = reloadData.moduleSearchType
    // state.searchType = reloadData.searchType

    gutils.merge(state, reloadData)
  }
}



