/**
 * 外部调用接口
 * 1.支持异步
 * 2.外部调用
 * 2.1 非模块的action调用: this.$store.dispatch('demoSetName', 'tzt')
 * 2.2 模块的action调用: this.$store.dispatch('demo/demoSetName', 'tzt')
 */
export default {

  setActiveIndex ({ commit }, index) {
    commit('setActiveIndex', index)
  },

  /**
   * 添加tag
   * @param commit
   * @param tag {
   *  saerchType: '',
   *  searchKey: '',
   * }
   */
  addTag ({ commit }, tag) {
    commit('addTag', tag)
  },

  closeTag ({ commit }, index) {
    commit('closeTag', index)
  },

  closeOtherTags ({ commit }, index) {
    commit('closeOtherTags', index)
  },

  // 清除所有的tag
  closeAllTags ({ commit }) {
    commit('closeAllTags')
  }

}



