/**
 * 外部调用接口
 * 1.支持异步
 * 2.外部调用
 * 2.1 非模块的action调用: this.$store.dispatch('demoSetName', 'tzt')
 * 2.2 模块的action调用: this.$store.dispatch('demo/demoSetName', 'tzt')
 */
export default {
  demoSetName ({ commit }, name) {
    commit('demoSetName', name)
  },

  demoSetAge ({ commit }, age) {
    commit('demoSetAge', age)
  },

  demoAsync ({ commit }, { age, name }) {
    commit('demoSetName', name)
    commit('demoSetAge', age)
  }
}



