/**
 * 通过key
 * @param arr
 * @param key_name
 * @param key_value
 * @returns {number}
 */
Utils.getIndexByValue = function (arr, key_name, key_value) {
    for (let i = 0; i < arr.length; ++i) {
        let item = arr[i]
        if (item[key_name] == key_value)
            return i
    }

    return -1
}

