/**
 检测是否是正确的电话号码
 (1)必须全为数字;
 (2)必须是11位.(有人说还有10位的手机号,这里先不考虑);
 (3)必须以1开头(有人见过以2开头的手机号吗?)
 (4)第2位是34578中的一个.
 * @param str
 * @returns {boolean}
 */
Utils.isPhone =function (str) {
    let reg = /^1[34578]\d{9}$/;
    return reg.test(str);
}

