/**
 * 克隆JS对象(深度拷贝)
 * @param obj 要克隆的对象
 * @returns {Clone}
 */
Utils.clone = function clone(obj) {
    var o;
    if (typeof obj == "object") {
        if (obj === null) {
            o = null;
        } else {
            if (obj instanceof Array) {
                o = [];
                for (var i = 0, len = obj.length; i < len; i++) {
                    o.push(clone(obj[i]));
                }
            } else {
                o = {};
                for (var j in obj) {
                    o[j] = clone(obj[j]);
                }
            }
        }
    } else {
        o = obj;
    }
    return o;
}


/**
 * 数字转字符串自动补全(格式化)
 * @param num 要补全的数值
 * @param n 总长度
 * @param pad_str 要不全的字符
 * @returns {*}

 pad(20, 1) => '20'
 pad(20, 4) => '0020'
 pad(20, 4, '1') => '1120'
 */
Utils.pad = function(num, n, pad_str='0') {
    var len = num.toString().length;
    while(len < n) {
        num = pad_str + num;
        len++;
    }
    return num;
}


