/**
 * 根据指定的索引列表切分字符串
 * eg."aabbaadd" 切分索引[0, 4], 长度 "aa".length = 2
 * @param str 要切分的字符串
 * @param arr_idxes 切分索引[0, 4]
 * @param len 切分的字符串的长度
 * @returns {Array} ["aa", "bb", "aa", "dd"]
 */
function split(str, arr_idxes, len) {
    let tlen = str.length
    let idx = 0

    let arr = []
    for (let i = 0; i < arr_idxes.length; ++i) {
        let v = arr_idxes[i]

        // console.log("v:" + v + " idx:" + idx)
        if (idx == v) {
            arr.push({
                idx: idx,
                len: len,
            })

            idx = v + len
        } else if (idx < v) {
            arr.push({
                idx: idx,
                len: v - idx,
            })

            arr.push({
                idx: v,
                len: len,
            })

            idx = v + len
        } else {
            arr.push({
                idx: idx,
                len: len
            })

            idx = v + len
        }
    }

    if (idx < tlen) {
        arr.push({
            idx: idx,
            len: tlen - idx
        })
    }

    let arrs = []
    arr.map((item, idx) => {
        let s = str.substring(item.idx, item.idx + item.len)
        arrs.push(s)
    })

    return arrs
}

/**
 * 根据key切分，并且保留key
 * @param str
 * @param key
 * @returns {Array} 切分后的字符串数组
 */
function _splitStringToArray(str, key) {
    let indexes = str.indexOfAll(key)
    let arrs = split(str, indexes, key.length)
    return arrs
}

/**
 * 一个字符数组,将匹配到的字符串再次细分成数组
 * @param arr_str 字符串数组
 * @param key 要查找的key
 * @returns {Array}
 */
function _splitStringArray(arr_str, key) {
    let arrs = []
    for (let i = 0; i < arr_str.length; ++i) {
        if (typeof arr_str[i] == 'string') {
            let arr = _splitStringToArray(arr_str[i], key)
            arrs = arrs.concat(arr)
        } else {
            arrs.push(arr_str[i])
        }
    }

    return arrs
}

/**
 * 根据keys列表来切分字符串
 * 根据第一个key，切分成数组。再根据第二个key，在切分的数组中再次查找并切分...
 * @param str
 * @param keys
 * @returns {Array.<*>}
 */
Utils.splitAllByKeys = function(str, keys) {
    let str_arr = [].concat(str)

    for (let i = 0; i < keys.length; ++i) {
        str_arr = _splitStringArray(str_arr, keys[i])
    }

    return str_arr
}

//-------------------------------------------遍历对象--------------------------------------------------------


/**
 * 遍历一个对象
 * @param obj 对象
 * @param cb (obj, key) => { }
 **/
Utils.traversalObject = (obj, cb) => {
  for(let k in obj){
    if(typeof obj[k] === "object"){
      this.traversalObject(obj[k], cb)
    }else{
      if (cb) cb(obj, k, obj[k])
    }
  }
}

/**
 * 遍历一个对象，并删除和deletes对象有交集的key
 * 备注：修改原有数据
 * @param obj 对象
 * @param cb (obj, key) => { }
 * @param deletes object
 **/
Utils.traversalDelete = (obj, deletes = {}) => {
  deletes = deletes || {}
  if (Object.keys(deletes).length <= 0)
    return

  this.traversalObject(obj, (o, k) => {
    if (deletes[k])
      delete o[k]
  })
}

/**
 * 遍历一个对象，并替换和replaces对象有交集的key
 * 备注：修改原有数据
 * @param obj 对象
 * @param cb (obj, key) => { }
 * @param deletes object
 **/
Utils.traversalReplace = (obj, replaces = {}) => {
  replaces = replaces || {}
  if (Object.keys(replaces).length <= 0)
    return

  this.traversalObject(obj, (o, k) => {
    if (replaces[k])
      o[k] = replaces[k]
  })
}


