/**
 * 所有计算时间的地方都调用此函数
 * @param desc
 */
Utils.tlog = function(desc) {
    if (!Utils.__startTime) {
        Utils.__startTime = new Date()

        if (desc) {
            console.log("timeLog >> --------------- startTime")
        }
        return
    }

    let now = new Date()
    let dt = new Date() - Utils.__startTime

    Utils.__startTime = now

    if (!desc)
        desc = "time"
    console.log(`timeLog >> --------------- ${desc}:${dt}`)
}

//判断字符串是否包换数字和字母
Utils.passwordFrom = function(pwd){
    let zimu = /[a-zA-Z]/i
    let shuzi = /[0-9]/i
    if (zimu.test(pwd)) {
        if (shuzi.test(pwd)) {
            return true
        }
    }
    return false
}

/**
 *
 * 查找数组，返回匹配到的第一个index
 *
 * @param array 被查找的数组
 * @param feature 查找特征 或者为一个具体值，用于匹配数组遍历的值，或者为一个对象，表明所有希望被匹配的key-value
 * @param or boolean 希望命中feature全部特征或者只需命中一个特征，默认true
 *
 * @return 数组下标  查找不到返回-1
 */
Utils.findArray = function (array, feature, all = true) {
    for(let index in array){
        let cur = array[index];
        if(feature instanceof Object){
            let allRight = true;
            for(let key in feature){
                let value = feature[key];
                if(cur[key] == value && !all) return index;
                if(all && cur[key] != value){
                    allRight = false;
                    break;
                }
            }
            if(allRight) return index;
        }else{
            if(cur == feature){
                return index;
            }
        }
    }
    return -1;
}

/**
 * 遍历一个字符串数组，只要其中有一个元素是以 指定key开始的，返回true, 否则false
 * @param strArr
 * @param key
 * @returns {boolean}
 */
Utils.stringArrayStartsWith = function(strArr, key) {
    for (let m = 0; m < strArr.length; ++m) {
        if (strArr[m].startsWith(key)) {
            return true
        }
    }

    return false
}

/**
 *  time分钟向上取整 分钟时间 -> t天h小时m分钟
 * @param min_time
 * @returns {string}
 */
Utils.getMinutesCeilFromMinTime = function (min_time) {
    let min = min_time      // 分钟
    let hour                // 小时
    let day                 // 天
    let time = ''           // 几天几小时几分钟

    if (min_time > 60) {
        min = min_time % 60
        hour = parseInt(min_time / 60)

        if (hour > 24) {
            hour = parseInt(min_time /60) % 24
            day = parseInt(min_time / 60 / 24)
        }
    }

    if (day) {
        time = day + '天'
    }
    if (hour) {
        time += hour+'小时'
    }
    if (min) {
        //TODO min 向上取整
        time += Math.ceil(min)+'分钟'
    }

    return time
}


/**
 * 根据类型计算时间的其实和终止日期
 * @param type 'day'、'week'、'month'、'year'
 * @param date
 */
Utils.calcDateRegion = function(type, date = new Date()) {
    let y = date.getFullYear()
    let m = date.getMonth()
    let d = date.getDate()
    // let maxd = new Date(y, m+1, 0).getDate()
    let maxd = new Date().getDate()
    let sm = String(m+1).padStart(2, '0')
    let sd = String(d).padStart(2, '0')

    let startDate
    let endDate

    switch(type) {
        case 'day':
        {
            startDate = `${y}-${sm}-${sd}`
            endDate = startDate
        }
            break
        case 'week':
        {
            let start = new Date(date)
            let end = new Date(date)
            start.setDate(date.getDate() - date.getDay() + 1)
            end.setDate(date.getDate() + 7 - date.getDay())

            let sm = String(start.getMonth()+1).padStart(2, '0')
            let sd = String(start.getDate()).padStart(2, '0')
            let em = String(end.getMonth()+1).padStart(2, '0')
            let ed = String(end.getDate()).padStart(2, '0')

            startDate = `${start.getFullYear()}-${sm}-${sd}`
            endDate =  `${end.getFullYear()}-${em}-${ed}`
        }
        break
        case 'month':
        {
            startDate = `${y}-${sm}-01`
            endDate = `${y}-${sm}-${maxd}`
        }
        break
        case 'year':
        {
            let lmonth_days = new Date(y, 12, 0).getDate()

            startDate = `${y}-01-01`
            endDate = `${y}-12-${lmonth_days}`
        }
        break
        default:
            alert("Unknown type:" + type)
            break
    }

    return {
        startDate,
        endDate,
    }
}

Utils.isMac = function (value) {
    var temp = /[A-Fa-f0-9]{2}:[A-Fa-f0-9]{2}:[A-Fa-f0-9]{2}:[A-Fa-f0-9]{2}:[A-Fa-f0-9]{2}:[A-Fa-f0-9]{2}/;
    if (!temp.test(value))
    {
        return false;
    }
    return true
}


Utils.isValidLong = (param) => {
    var temp = /^(\-|\+)?(((\d|[1-9]\d|1[0-7]\d|0{1,3})\.\d{0,6})|(\d|[1-9]\d|1[0-7]\d|0{1,3})|180\.0{0,6}|180)$/
    if (!temp.test(param)) {
      return false
    }
    return true
}
Utils.isValidLat = (param) => {
  var temp =  /^(\-|\+)?([0-8]?\d{1}\.\d{0,6}|90\.0{0,6}|[0-8]?\d{1}|90)$/
  if (!temp.test(param)) {
    return false
  }
  return true
}

/**
 * 分解一个url为map
 * @param url
 * @returns {{}}
 * name=tzt&phone=15800001111
 * =>
 * {
 * name: tzt,
 * phone: 15800001111
 * }
 */
Utils.splitUrlToObject = function (url) {
    const groups = url.split("&")

    let o = {}
    for (let idx in groups) {
        let item = groups[idx]

        let datas = item.split("=")
        if (datas && datas.length >= 2) {
            o[datas[0]] = datas[1]
        }
    }
    return o
}


const MOBILE = ["139","138","137","136","135","134","159","158","157","150","151","152","147","188","187","182","183","184","178"]
const UNICORN = ["130","131","132","156","155","186","185","145","176"]
const TELECOM =["133","153","189","180","181","177","173"]

/**
 * 获取手机号的类型
 * @param val
 * @returns {*}
 */
Utils.getPhoneType = function (phone) {
    phone = phone.trim()
    if (phone.length < 11) {
        return ''
    }

    let prefix = phone.substring(0, 3)

    if (MOBILE.includes(prefix))
        return "移动"

    if (UNICORN.includes(prefix))
        return "联通"

    if (TELECOM.includes(prefix))
        return "电信"

    return ''
}

/**
 * 浅合并，修改源数组
 * @param mergeToObj
 * @param obj
 */
Utils.merge = (mergeToObj, obj) => {
  for (let k in obj) {
    mergeToObj[k] = obj[k]
  }
}



//-------------------------------------------类型检查--------------------------------------------------------


/**
 * 参数类型检测
 * @type {{PCT_BASE: number, PCT_INTEGER: number, PCT_NAME: number, PCT_PHONE: number, PCT_IDCARD: number, PCT_BANK: number, PCT_QQ: number, PCT_IP: number, PCT_MAC: number, PCT_DOMAIN: number, PCT_LAT: number, PCT_LONG: number, PCT_ZONECODE: number, PCT_ZIPCODE: number, PCT_COMPANY: number, PCT_LOGISTICS_NAME: number, PCT_LOGISTICS_COMP: number, PCT_VIN: number}}
 */
Utils.ParamCheckType = {
  PCT_UNKNOWN: 0,
  PCT_BASE: 1,      // 通用：名字、电话、身份证....
  PCT_INTEGER: 2,  // 整数
  PCT_NAME: 3,
  PCT_PHONE: 4,
  PCT_IDCARD: 5,
  PCT_BANK: 6,
  PCT_QQ: 7,
  PCT_IP: 8,
  PCT_MAC: 9,
  PCT_DOMAIN: 10,        //  域名
  PCT_LAT: 11,           //  纬度
  PCT_LONG: 12,          //  经度
  PCT_ZONE_CODE: 13,     //  区号
  PCT_ZIP_CODE: 14,      //  邮编
  PCT_COMPANY: 15,      //  公司名
  PCT_LOGISTICS_ORDER_ID: 16,   //  物流快递单号
  PCT_LOGISTICS_COMP: 17,   //  物流公司的编号
  PCT_VIN: 18,              //  车架号
  PCT_NUMBER: 19,
}


Utils.ParamCheckError = {
  OK: 0,
  ERR: 1,             // 通用错误描述
  MIN_LENGTH: 2,      // 最短长度不满足要求
  MAX_LENGTH: 3,      // 最大长度不满足要求
}

/**
 * 检测类型
 * @param param 要检测的参数
 * @param param_check_type
 * @param ex_params {
 *  minLength: 6,      // 最短位数
 *  maxLength: 10,     // 最长位数
 * }
 */
Utils.checkParam = (param, param_check_type, ex_params = {}) => {
  if (!param) {
    return {
      code: Utils.ParamCheckError.OK,
      reason: '',
    }
  }
  if (typeof param !== 'string')
    param = '' + param

  let min_length = ex_params.minLength || 0
  let max_length = ex_params.maxLength || 32
  if (param.length < min_length) {
    return {
      code: Utils.ParamCheckError.MIN_LENGTH,
      reason: '最短长度不满足要求',
    }
  }
  if (param.length > max_length) {
    return {
      code: Utils.ParamCheckError.MAX_LENGTH,
      reason: '最大长度不满足要求',
    }
  }

  switch (param_check_type) {
    case Utils.ParamCheckType.PCT_PHONE: {
      let b = Utils.isPhone(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : '手机号码格式错误'
      }
    }
    break
    case Utils.ParamCheckType.PCT_IDCARD: {
      let b = Utils.isValidID(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : '身份证号码格式错误'
      }
    }
      break
    case Utils.ParamCheckType.PCT_BANK: {
      let b = Utils.isBankID(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : '银行卡号格式错误'
      }
    }
      break
    case Utils.ParamCheckType.PCT_QQ: {
      let b = Utils.isQQ(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : 'QQ号格式错误'
      }
    }
      break
    case Utils.ParamCheckType.PCT_IP: {
      let b = Utils.isIP(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : 'ip地址格式错误'
      }
    }
      break
    case Utils.ParamCheckType.PCT_MAC: {
      let b = Utils.isMac(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : 'mac地址格式错误'
      }
    }
      break
    case Utils.ParamCheckType.PCT_DOMAIN: {
      let b = Utils.isURL(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : '网址格式错误'
      }
    }
      break
    case Utils.ParamCheckType.PCT_LAT: {
      let b = Utils.isValidLat(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : '纬度格式错误：纬度区间为-90~90，纬度精确到小数点后1-6位'
      }
    }
      break
    case Utils.ParamCheckType.PCT_LONG: {
      let b = Utils.isValidLong(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : '经度格式错误：经度区间为-180~180,经度精确到小数点后1-6位'
      }
    }
      break
    case Utils.ParamCheckType.PCT_VIN: {
      let b = Utils.isVIN(param)
      return {
        code: b ? Utils.ParamCheckError.OK : Utils.ParamCheckError.ERR,
        reason: b ? '' : 'vin车架号格式错误'
      }
    }
      break
  }

  return {
    code: Utils.ParamCheckError.OK,
    reason: '',
  }
}


export default Utils;











































