
// 初始化历史记录列表
function initHistoryList () {
  let historys = {}
  for (let k in gconst.SearchType) {
    let search_type = gconst.SearchType[k]

    let arr = []
    for (let i = 0; i < gconst.MAX_HISTORY_PARAMS_PER_TYPE; ++i) {
      arr.push([])
    }
    historys[search_type] = arr
  }

  return historys
}

/**
  {
    [gconst.SearchType.PHONE_STATE] = [
      ['15800001111'],
      [],
      [],
      [],
      [],
    ]
  }
 */
let historys = initHistoryList()

export default historys



