/**
 * 1.模块调用: this.$store.getters['me/getName']
 * 2.mapGetters映射: ...mapGetters('me', ['getName']),
 * 参考: https://blog.csdn.net/qq_39523111/article/details/79638614
 */
export default {
  getHistoryByIndex: (state, searchType, paramIndex) => {
    // alert("getHistoryByIndex type:" +  JSON.stringify(searchType) + " idnex:" + JSON.stringify(paramIndex))
    if (state[searchType] && state[searchType][paramIndex])
      return state[searchType][paramIndex]

    return state
  }
}



