/****************************************分类规则**************************************************/

/**
 * 遍历一个对象
 * @param obj 对象
 * @param cb (obj, key) => { }
 **/
function traversalObject(obj, cb){
    for(let k in obj){
        if(typeof obj[k] === "object"){
            traversalObject(obj[k], cb)
        }else{
            if (cb) cb(obj, k, obj[k])
        }
    }
}

/**
 * 遍历一个对象，并删除和deletes对象有交集的key
 * 备注：修改原有数据
 * @param obj 对象
 * @param cb (obj, key) => { }
 * @param deletes object
 **/
function traversalDelete(obj, deletes = {}) {
    deletes = deletes || {}
    if (Object.keys(deletes).length <= 0)
        return

    traversalObject(obj, (o, k) => {
        if (deletes[k])
            delete o[k]
    })
}

/**
 * 遍历一个对象，并替换和replaces对象有交集的key
 * 备注：修改原有数据
 * @param obj 对象
 * @param cb (obj, key) => { }
 * @param deletes object
 **/
function traversalReplace(obj, replaces = {}) {
    replaces = replaces || {}
    if (Object.keys(replaces).length <= 0)
        return

    traversalObject(obj, (o, k) => {
        if (replaces[k])
            o[k] = replaces[k]
    })
}


