/**
 * 数据分组
 * @param obj 源数据data下的对象。eg.ServerResData.data[0]、ServerResData.data[1]
 * @param search_type 查询类型
 * @param customRule 自定义的规则
 默认：基础数据(默认所有字段都放到这个下面)，  1.更改名字 2.分出新的，修改旧的

 * @return {Array}
 data = [{
            "title": "基础数据",
            "data": [{
                    "k": "country",
                    "v": "15828225532",
                    "kc": "中国"
                },
                ...
            ]
        }, {
            "title": "多领域01",
            "data": [{
                "k": "lat",
                "v": "30.554260",
                "kc": "维度"
            },
            ...
            ]
        }, {
            "title": "多领域02",
            "data": [{
               "k": "lat",
               "v": "30.558994",
               "kc": "维度"
            },
            ...
            ]
        }]

 */
function processClassify(obj, search_type, translates={}) {
    if (!obj || typeof obj !== 'object' || obj instanceof Array) {
        alert("unknown key type!")
        return {}
    }

    let rule = getClassifyRule(search_type)
    /**
     {
        title: '基础数据',
        equals: [],           // 必然包含的字段, isDefault = true时，所有剩余的字段都放到当前字段下
        isDefault: true,
    },
     {
         title: '手机状态',
         equals: ['phoneStatus'],
         isDefault: false,
     },
     {
         title: '在网时长',
         equals: ['duration'],
         isDefault: false,
     },
     */

        // 1.填充分类规则【default】字段下的【equals】列表
    let new_groups = gutils.clone(rule)

    let defaultGroup = {}
    let keyList = []

    for (let idx in new_groups) {
        let r = new_groups[idx]

        if (r && r.isDefault) {
            defaultGroup = r
        } else {
            let ks = r.equals || []
            keyList = [...keyList, ...ks]
        }
    }

    let keys = {}
    for (let idx in keyList) {
        keys[keyList[idx]] = true
    }

    let specialList = []            // 第一项为数据的元素，单独处理[{k: 'MultiAreas', arr:[{lon:10, log:20}, ...]
    let def_keys = []
    for (let k in obj) {
        if (obj[k] instanceof Array) {
            specialList.push({
                skey: k,
                arr: obj[k]
            })
        } else if (typeof obj[k] !== 'object' && !keys[k]) {
            def_keys.push(k)
        }
    }

    defaultGroup['equals'] = def_keys
    // [{"title":"基础数据","equals":["zipCode","areaCode","address","phone","operators","prefix","cardType","name"],"isDefault":true},{"title":"手机状态","equals":["phoneStatus"],"isDefault":false},{"title":"在网时长","equals":["duration"],"isDefault":false}]


    // 2.转换成标准的数据格式
    // 2.1 先转rules下需要的字段
    let latitudeFlag = false
    let longitudeFlag = false
    for (let i in new_groups) {
        let group = new_groups[i]
        let equals = group.equals

        let olist = []
        for (let j in equals) {
            let k = equals[j]
            let v = typeof obj[k] === 'string' ? obj[k] : String(obj[k] )
            let kc = translates[k]
            if (k == 'longitude')
                longitudeFlag = true
            if (k == 'latitude')
                latitudeFlag = true
            let o = {
                k,
                v: v,
                kc: kc || ''
            }


            // 自动识别k、v的类型
            parseObjectLinkType(o, k, v)

            olist.push(o)
        }

        /*---------------客户端手动添加数据---------------*/
        let indexOfMapSearch = gconst.MAP_SEARCH_TYPES.indexOf(search_type)
        if (indexOfMapSearch !== -1 && latitudeFlag && longitudeFlag) {
            let oe = {
                k: 'map',
                v: '',
                kc: '地图',
                linkType: gconst.LinkType.MAP,
            }
            olist.push(oe)
        }
        /*---------------------end--------------------*/

        group['data'] = olist
        group['contains'] = undefined
        group['equals'] = undefined
        group['isDefault'] = undefined
    }

    // 2.2 处理第一层下的 数组对象列表 => 数组对象 => 每一行
    latitudeFlag = false
    longitudeFlag = false
    for (let i in specialList) {
        const {skey, arr}  = specialList[i]
        let title = translates[skey]

        for (let j in arr) {
            let item = arr[j]
            let olist = []

            for (let k in item) {
                let v = item[k]
                let kc = translates[k]
                if (k =='longitude' || k == 'lng')
                    longitudeFlag = true
                if (k == 'latitude' || k == 'lat')
                    latitudeFlag = true
                let o = {
                    k,
                    v,
                    kc: kc || ''
                }

                // 自动识别k、v的类型
                parseObjectLinkType(o, k, v)

                olist.push(o)
            }

            /*---------------客户端手动添加数据---------------*/
            let indexOfMapSearch = gconst.MAP_SEARCH_TYPES.indexOf(search_type)
            if (indexOfMapSearch !== -1 && latitudeFlag && longitudeFlag) {
                let oe = {
                    k: 'map',
                    v: '',
                    kc: '地图',
                    linkType: gconst.LinkType.MAP,
                }
                olist.push(oe)
            }
            /*---------------------end--------------------*/

            new_groups.push({
                title: `${title}${parseInt(j) + 1}`,
                data: olist,
            })
        }
    }

    return new_groups
}



