/**
 * 遍历解析一个数组的所有linkType
 * @param result {Object} 服务器返回的raw_data {code: 0, translate: {}, data: [{}, {}, ...] }
 */
function parseLinkType(result) {
    if (!result || typeof result !== 'object')
        return

    let dts = result.translate !== undefined || result.code !== undefined ?
                    result.data
                    :
                    (result instanceof Array ? result : [result])

    for (let elem of dts) {
        let data_list = elem.data
        if (data_list) {
            for (let obj of data_list) {
                parseObjectLinkType(obj, obj.k, obj.v)
            }
        }
    }
}





function getSearchDemoData(type, isNormalizeData=true) {
    let dt = _DemoDatas[type]
    if (!dt) {
      console.log("getSearchDemoData type is not exist! type:" + type)
      return
    }

    let res = gutils.clone(dt)
    if (isNormalizeData)
      res = normalizeData(res, type)
    return res
}




// 几种处理失败的情况
function _processFailed(cbFailed, sourceData) {
    if (!cbFailed)
        return
    const {code, message} = sourceData
    let linkType = gconst.LinkType.ERR
    let msg = '错误'
    switch(code)
    {
        case -101:
        case -2:
        case 1:
        case 3:
        case 7:
        case 10000:
        case 10001:
        case 10002:
        case 10003:
        {
            linkType = gconst.LinkType.ERR
            msg = message
        }
            break
    }

    let data =
        {
            "code": `${code}`,
            "reason": `${message}`,

            // 当前数据会存贮
            "dataType": 0,     //0正常数据(默认) 1模拟数据
            data: [{
                "title": "查询结果",
                "data": [{
                    "k": "",
                    "v": `${msg}`,
                    'linkType': gconst.LinkType.ERR
                }]
            }]
        }

    cbFailed(data, sourceData)
}

