function _requestDatas(url, params, searchType) {
  console.log ('search request : url----------: ' + url +  ' params:' + JSON.stringify(params))
  return axios.get(`${gbase}${url}`, { params: params } ).then(res => {
    console.log ('search result: type-------: ' + searchType + ' resData: '  +  JSON.stringify(res) )
    let ret = res && res.data ? res.data : res
    return Promise.resolve(ret)
  } , err => {
    return Promise.reject(err)
  }).catch( err => {
    return Promise.reject(err)
  })
}




