/**
 * 检测输入key是否正确
 * @param searchType
 * @param params
 * @returns {boolean}
 */
function checkSearchKey (searchType, params = []) {
    if (!(params instanceof Array)) {
        return {isTrue: false, params}
    }

    if (params.length <= 0) {
        return {isTrue: false, params}
    }

    let key = params[0]
    key = key.trim()
    if (key.length <= 0) {
        return {isTrue: false, params}
    }

    switch(searchType) {
        case gconst.SearchType.PHONE_STATE:
        case gconst.SearchType.NETWORK_TRACES:
        case gconst.SearchType.LOGISTICS_REALTIME:
        {
            let isTrue = gutils.isPhone(key)
            return {isTrue, params}
        }
            break
        case gconst.SearchType.BANK_PLUS:
        case gconst.SearchType.BANK_REAL:
        {
            let isTrue = gutils.isBankID(key)
            return {isTrue, params}
        }
        case gconst.SearchType.IP_PLUS:
        case gconst.SearchType.IP:
        {
            let isTrue = gutils.isIP(key)
            return {isTrue, params}
        }
        case gconst.SearchType.AIRPLANE_VALUE: {
            let isTrue = gutils.isValidID(key)
            return {isTrue, params}
        }
        case gconst.SearchType.MAC_ADDRESS: {
            key = key.replaceAll(' ', '')
            key = key.toLowerCase()

            let s = ''
            for (let i = 0; i < key.length; ++i) {
                let k = key[i]
                if (k >= 'a' && k <= 'f' || k >= '0' && k <= '9') {
                    s += k
                }
            }

            if (s.length !== 12) {
                return {isTrue: false, params: [key]}
            }

            let newKey = ''
            for (let i = 0; i < s.length; i += 2) {
                newKey += i === 0 ? `${s.substr(i, 2)}`:`:${s.substr(i, 2)}`
            }

            let isTrue = gutils.isMac(newKey)
            return {isTrue, params: [newKey]}
        }
        case gconst.SearchType.VIN:
        case gconst.SearchType.WHOIS:
        case gconst.SearchType.LOGISTICS:
            break

    }

  return {isTrue: true, params}
}

