
let LocalStorageManager = {}

LocalStorageManager.save = (module_name) => {
  if (!module_name)
    return

  let account = localStorage.getItem('latestAccount')

  // 从localStorage中重新加载数据
  let store = vue.$store
  console.log("------------------------------------------state")

  let data = store.state[module_name] || {}
  let json_data = JSON.stringify(data)
  console.log("----------------------save storage module:" + module_name +  " json_data:" + json_data)
  localStorage.setItem(`${account}_${module_name}`, json_data)   // '15800001111_me'
}

LocalStorageManager.reload = () => {
  if (!localStorage.getItem('access_token')) {
    let account = localStorage.getItem('latestAccount')

    // 从localStorage中重新加载数据
    let store = vue.$store
    console.log(store)
    for ( let k in store._modulesNamespaceMap) {      // { 'demo/', 'me/', 'search/', ... }
      let func_name = k + 'reloadFromStorage'
      if (store._mutations[func_name]) {
        let uk = k.slice(0, k.length - 1)
        let json_data = localStorage.getItem(`${account}_${uk}`)   // '15800001111_me'
        console.log("load json_data:" + json_data)
        let data = JSON.parse(json_data || '{}')

        store.commit(func_name, data)
      }
    }
  }
}



export default LocalStorageManager



