      //     meta: { title: '微博详情' }
      //   },
      //   {
      //     path: '/page/BusinessEnterprise',
      //     component: resolve => require(['@/components/page/BusinessEnterprise.vue'], resolve),
      //     meta: { title: '工商企业' }
      //   },
      //   {
      //     path: '/page/BusinessEnterpriseDetails',
      //     component: resolve => require(['@/components/page/BusinessEnterpriseDetails.vue'], resolve),
      //     meta: { title: '工商企业详情' }
      //   },
      //   {
      //     path: '/page/InternetTrack',
