/**
 * 外部调用接口
 * 1.支持异步
 * 2.外部调用
 * 2.1 非模块的action调用: this.$store.dispatch('demoSetName', 'tzt')
 * 2.2 模块的action调用: this.$store.dispatch('demo/demoSetName', 'tzt')
 */
export default {
  currentChannel ({commit}, currentChannel) {
    commit('getCurrentChannel', currentChannel)
  },
  obtainSearchContent ({commit}, searchContent) {
    commit('getSearchContent', searchContent)
  },
  searchBar ({commit}, searchBarBlock) {
    commit('getSearchBarBlock', searchBarBlock)
  },
  goBackSearchPage ({commit}, returnSearchPageBlock) {
    commit('getBack', returnSearchPageBlock)
  },
  obtainGetLabelsType ({commit}, labelsType) {
    commit('getLabelsType', labelsType)
  },
  obtainGetLabelsIndex ({commit}, labelsIndex) {
    commit('getLabelsIndex', labelsIndex)
  },
  // bEDetailsName ({commit}, bEDetailsName) {
  //   commit('obtainBEDetailsName', bEDetailsName)
  // }setModuleAndModuleSearchType

  setModuleAndModuleSearchType ({commit}, payload) {
    commit('setModuleAndModuleSearchType', payload)
  },

  setSearchType ({commit}, searchType) {
    commit('setSearchType', searchType)
  },
}



