/**
 * 全局code处理
 * @param code
 * @param resData
 */
function defaultCodeHandler (code, resData) {
  let reason = resData.failure || resData.reason || '未知错误!'
  switch(code) {
    case gconst.ERR.TOKEN_TIMEOUT:
    case gconst.ERR.OTHER_LOGINED:
    {
      //  清除token信息并跳转到登录页面
      localStorage.removeItem('isLogin')
      localStorage.removeItem('access_Token')

      window.vue.$confirm(reason, '重新登录', {
        confirmButtonText: '确定',
        type: 'warning',
        showCancelButton: false,
        center: true
      }).then(() => {
        // 只有在当前路由不是登录页面才跳转
        router.currentRoute.path !== '/login' ? router.push('/login') : ''
      }).catch(() => {
        router.currentRoute.path !== '/login' ? router.push('/login') : ''
      })
    }
      break
    default:
      window.vue.$message({
        showClose: true,
        message: reason,
        type: 'warning',
        center: true,
        duration: 1500
      })
      break
  }

  return Promise.reject({
    code: code,
    failure: reason,
    reason: reason
  })
}

