// http request 拦截器
axios.interceptors.request.use(
  config => {
    // console.log("--------server request:" + JSON.stringify(config))
    if (localStorage.getItem('access_Token')) {
      config.headers.accessToken = `${localStorage.getItem('access_Token')}`
      console.log("--------server accessToken:" + JSON.stringify(config.headers.accessToken))
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)

// http response 拦截器
axios.interceptors.response.use(
  response => {
    // console.log("-----------server response:" + JSON.stringify(response))

    // 特殊处理 【获取登录验证码】这个奇葩的消息，服务器配合修改
    if (response.data instanceof ArrayBuffer) {
      return response
    }
    // let ret = gfuncs.normalizeApiResult(response.data)
    // console.log('路由拦截', response)
    if (response.data && response.data.code === 1) {
      return response
    } else {
      return defaultCodeHandler(response.data.code, response.data)
    }
  },
  error => {
    // console.log("------------hook error:" + JSON.stringify(error))
    if (error.response) {
      switch (error.response.status) {

      }
    }
    // console.log(JSON.stringify(error));//console : Error: Request failed with status code 402
    return Promise.reject(error.response.data)
  }
)

