//获取超级查询的所有标签内容
api.getSuperSearchLablesUrl = () => {
  let datas = DDSuperSearch.data
  let urlArr = []
  for (let k in datas) {
    let itemArr = datas[k]
    for (let i = 0 ; i < itemArr.length ; i++) {
      let type = itemArr[i].type
      let url = `/api/v1/labels/indices/${type}/types/${type}`
      urlArr.push(url)
    }
  }
  return urlArr
}

//超级查询的所有标签查询
api.searchAllSuperLabels = params => {
  let requestUrls = api.getSuperSearchLablesUrl()
  for (let i = 0 ; i < requestUrls.length ; i++) {
    let url = requestUrls[i]
    // console.log ('url-----1: ', url)
    return axios.get(`${base}${url}`, { params: params }).then(res => {
      // console.log ('url------: ', JSON.stringify(res))
    })
  }
}

