/**
 * 注意: 不能将参数state重新赋值，只能更改变量，否则会导致state更新失败
 * eg.禁止 state = { ...state, ...params }
 */
export default {
  addHistory (state, history) {
    const {searchType, searchKey, index} = history
    if (!history) return
    if (!history.searchKey ||
      history.searchKey === undefined ||
      history.index === undefined ||
      history.index < 0
    ) {
      alert("history params error!")
      return
    }

    let arr_types = state[searchType]
    let arr_params = arr_types[index]
    let old_idx = arr_params.indexOf(searchKey)
    if (old_idx >= 0 && old_idx < arr_params.length)
      arr_params.splice(old_idx, 1)
    arr_params.splice(0, 0, searchKey)
  },


  clearHistoryByIndex (state, searchType, paramIndex) {
    let arr_types = state[searchType]
    let arr_params = arr_types[paramIndex]

    arr_params.splice(0, arr_params.length)
  },

  clearHistoryByType (state, searchType) {
    let arr_types = state[searchType]
    for (let i = 0; i < arr_types.length; ++i) {
      let arr_params = arr_types[i]
      arr_params.splice(0, arr_params.length)
    }
  },

  clearAllHistorys (state) {
    for(let k in state) {
      let arr_types = state[k]
      for (let i = 0; i < arr_types.length; ++i) {
        let arr_params = arr_types[i]
        arr_params.splice(0, arr_params.length)
      }
    }
  }
}



