/**
 * 外部调用接口
 * 1.支持异步
 * 2.外部调用
 * 2.1 非模块的action调用: this.$store.dispatch('demoSetName', 'tzt')
 * 2.2 模块的action调用: this.$store.dispatch('demo/demoSetName', 'tzt')
 */
export default {
  /**
   * 添加历史来记录
   * @param commit
   * @param history {
   *    searchKey: '15800001111',
   *    searchType: gconst.SearchType.PHONE_STATE
   *    index: 0,     // 最大为5
   * }
   */
  addHistory ({commit}, history) {
    if (!history) return
    if (!history.searchKey ||
      history.searchKey === undefined ||
      history.index === undefined ||
      history.index < 0
    ) {
      alert("history params error!")
      return
    }

    commit('addHistory', history)
  },


  clearHistoryByIndex ({commit}, searchType, paramIndex) {
    commit('clearHistoryByIndex', searchType, paramIndex)
  },

  clearHistoryByType ({commit}, searchType, searchKey) {
    commit('clearHistoryByType', searchType, searchKey)
  },

  clearAllHistorys ({commit}) {
    commit('clearAllHistorys')
  }
}



